<template>
	<view class="content">
		<!-- 状态栏 -->
		<view :style="{height: statusBarHeight + 'px'}"></view>
		<view class="top-bar" :style="{height: navBarHeight + 'px', width: windowWidth + 'px'}">
			<view class="top-bar-left">
				<image src="../../static/images/img/one.jpg" mode="aspectFill"></image>
			</view>
			<view class="top-bar-center">
				<image src="../../static/images/index/logo.png" mode="aspectFill"></image>
			</view>
			<view class="top-bar-right">
				<view class="search">
					<image src="../../static/images/index/search.png" mode="aspectFill"></image>
				</view>
				<view class="add">
					<image src="../../static/images/index/add.png" mode="aspectFill"></image>
				</view>
			</view>
		</view>
		<view class="main">
			<view class="friend-list">
				<view class="friend-list">
					<view class="friend-list-l">
						<text class="tip">1</text>
						<image src="../../static/images/index/apply.png" mode="aspectFill"></image>
					</view>
					<view class="friend-list-r">
						<view class="top">
							<view class="name">好友申请</view>
							<view class="time">13:00</view>
						</view>
						<view class="news">茫茫人海，相聚便是缘分</view>
					</view>
				</view>
			</view>
			<view class="friend-list" v-for="item in friends" :key="item.id">
				<view class="friend-list">
					<view class="friend-list-l">
						<text class="tip">{{item.tip}}</text>
						<image :src="item.imgurl" mode="aspectFill"></image>
					</view>
					<view class="friend-list-r">
						<view class="top">
							<view class="name">{{item.name}}</view>
							<view class="time">{{changeTime(item.time)}}</view>
						</view>
						<view class="news">{{item.news}}</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import data from '../../commons/js/data.js'
	import myfun from '../../commons/js/myfun.js'
	export default {
		data() {
			return {
				friends: [],
				statusBarHeight:　20,
				navBarHeight: 45,
				windowWidth: 375,
			}
		},
		onLoad() {
			this.getFriends()
		},
		created() {
			// 获取手机系统信息
			const info = uni.getSystemInfoSync()
			//设置状态栏高度
			this.statusBarHeight = info.statusBarHeight
			console.log(info)
			this.windowWidth = info.windowWidth
			
			// h5 app mp-alipay
			// #ifndef H5 || APP-PLUS || MP-ALIPAY
			// 获取胶囊的位置
			const menuButtonInfo = uni.getMenuButtonBoundingClientRect()
			console.log(menuButtonInfo)
			
			// (胶囊底部高度 - 状态栏的高度) + (胶囊顶部高度 - 状态栏内的高度) = 导航栏的高度
			this.navBarHeight = (menuButtonInfo.bottom - info.statusBarHeight) + (menuButtonInfo.top - info.statusBarHeight)
			console.log(this.navBarHeight)
			
			this.windowWidth = menuButtonInfo.left
			// #endif
		},
		methods: {
			changeTime: function(data) {
				return myfun.dataTime(data)
			},
			
			getFriends: function() {
				this.friends = data.friends()
				for(let i = 0; i < this.friends.length; i++) {
					this.friends[i].imgurl = '../../static/images/img/' + this.friends[i].imgurl
				}
				console.log(this.friends)
			}
		}
	}
</script>

<style lang="scss">
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.top-bar {
		position: sticky;
		left: 0;
		top: 0;
		z-index: 1000;
		width: 100%;
		height: 88rpx;
		display: flex;
		align-items: center;
		padding-top: var(--status-bar-height);
		box-sizing: border-box;
		background: $uni-bg-color;
		border-bottom: 1px solid $uni-border-color;
		overflow: hidden;

		.top-bar-left {
			width: 68rpx;
			height: 68rpx;
			padding-left: $uni-spacing-col-base;

			image {
				width: 68rpx;
				height: 68rpx;
				border-radius: 16rpx;
			}
		}

		.top-bar-center {
			flex: 1;
			text-align: center;

			image {
				width: 88rpx;
				height: 42rpx;
			}
		}

		.top-bar-right {
			display: flex;
			z-index: 1001;
			text-align: center;
			padding-right: 14rpx;

			.search {
				width: 88rpx;
				height: 88rpx;
			}

			.add {
				width: 88rpx;
				height: 88rpx;
			}

			image {
				width: 52rpx;
				height: 52rpx;
				padding: 18rpx 0;
			}
		}
	}
	
	.main {
		padding-top: 16rpx;
		width: 100%;
		box-sizing: border-box;
		.friend-list {
			display: flex;
			height: 96rpx;
			padding: 16rpx;
			align-items: center;
			&:active {
				background-color: $uni-bg-color-grey;
			}
			.friend-list-l {
				position: relative;
				width: 96rpx;
				height: 96rpx;
				image {
					width: 100%;
					height: 100%;
					border-radius: $uni-border-radius-base;
					background-color: $uni-color-primary;
				}
				.tip {
					position: absolute;
					z-index: 10;
					top: -6rpx;
					left: 68rpx;
					min-width: 36rpx;
					height: 36rpx;
					background: $uni-color-warning;
					border-radius: $uni-border-radius-circle;
					font-size: $uni-font-size-sm;
					color: $uni-text-color-inverse;
					line-height: 36rpx;
					text-align: center;
				}
			}
		
			.friend-list-r {
				padding-left: $uni-spacing-col-base; 
				width: 590rpx;
				box-sizing: border-box;
				display: flex;
				flex-direction: column;
				.top {
					display: flex;
					justify-content: space-between;
					height: 58rpx;
					.name {
						font-size: 36rpx;
						font-weight: 400;
						color: $uni-text-color;
						line-height: 50rpx;
					}
					.time {
						font-size: $uni-font-size-sm;
						color: $uni-text-color-disable;
					}
				}
				
				.news {
					font-size: $uni-font-size-base;
					color: $uni-text-color-grey;
					line-height: 40rpx;
					display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 1;
					overflow: hidden;
				}
			}
		}
	}
</style>
